$pic-diameter2: 40px;

.blogpost-container {
  text-align: center;
  padding-bottom: 50px;
  form {
    display: inline-block; }
  #article_banner {
    width: 100%;
    height: 400px;
    background-size: 100% 400px; }
  #content {
    margin: auto;
    width: 70%;
    margin-bottom: 50px;
    .social {
      display: inline;
      text-align: right;
      margin-top: 50px;
      margin-bottom: 50px;
      a {
        width: 35px;
        height: 35px;
        background-size: 35px;
        display: inline-block;
        margin: 5px;
        &#twitter {
          background-image: url("/images/icons/twitter.png"); }
        &#facebook {
          background-image: url("/images/icons/facebook.png"); }
        &#google {
          background-image: url("/images/icons/google.png"); } } }
    .author-container {
      //float:left
      display: inline-block;
      margin-top: 50px;
      text-align: left;
      width: 60%;
      .profile-pic {
        width: $pic-diameter2;
        height: $pic-diameter2; }

      p {
        text-align: left;
        &.header {
          font-style: italic;
          color: rgb(139, 139, 139); }
        &#author {
          display: inline-block;
          position: relative;
          bottom: calc(#{$pic-diameter2} - #{$pic-diameter2}*0.7);
          margin-left: 7px; } } }
    p {
      text-align: center;
      &#title {
        font-size: 30px; }
      &#intro {
        font-size: 20px; }
      &#body {
        font-size: 14px;
        margin-bottom: 50px;
        text-align: left; } } } }
